		methods:{
			gethuodongxinxiList() {
				let params = {
					page: 1,
					limit: 20,
				}
				this.$http({
					url: "huodongxinxi/page",
					method: "get",
					params: params
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.huodongxinxiList = data.data.list;
						let div = document.getElementById('huodongxinxiTable')
						var scrollTop = -1
						function move() {
							if (div){
								if(div.scrollTop==scrollTop){
									div.scrollTo({
										top: 0, 
										behavior: "smooth"
									});
									return false
								}
								scrollTop = div.scrollTop
								div.scrollTo({ 
									top: div.scrollTop + 40, 
									behavior: "smooth"
								});
							}
								
						}
						this.huodongxinxiTimer = setInterval(move, 2000);
						div.addEventListener('mouseenter', e => {
							e.stopPropagation()
							clearInterval(this.huodongxinxiTimer);
						})
						div.addEventListener('mouseleave', e => {
							e.stopPropagation()
							this.huodongxinxiTimer = setInterval(move, 2000);
						})
					}
				});
			},
			backClick(){
				this.$router.replace({ path: "/" });
			},
